
import {action, extendObservable} from 'mobx';
import {Modal} from 'antd-mobile';
import 'whatwg-fetch';

const alert = Modal.alert;
class AppState{

    constructor(){

        extendObservable(this,{
            topics:[],
            topicsPage:1,
            accessToken:null,
            messages:null,
            details:null,
            user:null,
            loading:true,

            emptyData: action(function(){ //
                this.topics.replace('');
            }),

            loadData: action(function(data={}){//
                this.loading = true;
                fetch(`/topics?page=${data.page || 1}&tab=${data.tab || 'all'}&limit=${data.limit || 11}`)
                .then(res => res.json() )
                .then((res) =>{
                    if(!res.success){
                        throw new Error(res.error_msg);
                    }else{
                        this.loading = false;
                        this.topics.replace(res.data);
                    }
                })  
                this.loading = true;
            }),

            fetchDetails: action(function(id){ //

                fetch(`/topic/${id}`)
                    .then(function(response) {
                        return response.json()
                    }).then((res)=>{
                        this.details=res.data; 
                    }).catch(function(ex) {
                        alert('')
                    })
            }),

            fetchMessage: action(function(accesstoken){//
                fetch(`/messages?accesstoken=${accesstoken || this.accessToken}`)
                .then(function(response) {
                    return response.json()
                }).then((res)=>{
                    this.messages = res.data
                }).catch(function(ex) {
                    alert('')
                })
            }),
            fetchUser: action(function(loginname){//
                fetch('/user/'+loginname+'').then(function(data){
                    return data.json()
                  }).then((res)=>{
                    if(res.success){
                        this.user = res
                    }
                  }).catch(function(ex){
                    alert('')
                })
            }),
        })
    }
}

export default AppState; 




